@import '../App.css';
.about {
    height: auto;
}
.tall {
    width: fit-content;
    max-height: 625px;
}
.about .container {
    gap: 3rem;
}
.about .left {
    width: auto;
}
.about .right {
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.about .right h1 {
    color: var(--darker-green);
}
.about .right {
    gap: 1rem;
}
.right p {
    font-size: 1.25rem;
}
.right .dark-green, .right .olive-green {
    font-weight: 700;
}
@media all and (max-width: 960px) {
    .about .left {
        display: flex;
        justify-content: center;
    }
    .about
    .container {
        flex-direction: column;
        text-align: left;
    }
}